export default {
	data() {
		return {
			tempFace:""
		}
	},
	onLoad(params){
		var tempFilePath = params.tempFilePath;
		this.tempFace = tempFilePath;
	},
	methods: {
		//重新选择头像
		changePendingFace(){
			var _this = this;
			//上传头像
			uni.chooseImage({
				count:1,
				success(res) {
					//获得临时路径
					var tempFilePath = res.tempFilePaths[0];
					_this.tempFilePath = tempFilePath;
				}
			})
		}
	}
}
